* {
    outline: none;
}

body {
    margin: auto auto;
    font-family: 'Roboto', sans-serif;

}
h2,
h3,
h4 {
    font-family: 'Playfair Display', serif;
    margin: 0;
}

ul {
    padding: 0;
}

li {
    list-style-type: none;
}
a {
    text-decoration: none;
    color: inherit;
    display: contents;
}
p {
    margin: 0;
}
