.pagination {
    display: flex;
    justify-content: space-between;
    width: 426px;
    margin: auto;
    margin-bottom: 87px;
}

.list {
    display: flex;
    justify-content: center;
}

.item {
    font-size: 14px;
    line-height: 17px;
    margin-right: 5px;
    color: #000000;
    opacity: 0.4;
    cursor: pointer;
}

.active {
    font-size: 20px;
    line-height: 24px;
    opacity: 1;
    margin-top: -5px;
    border-bottom: 2px solid #ff7931;
}
.arrow {
    cursor: pointer;
}
.arrowLeft {
    width: 30px;
    height: 30px;
    background-image: url('../images/arrow-left.svg');
}
.arrowRight {
    width: 30px;
    height: 30px;
    background-image: url('../images/arrow-right.svg');
}
@media (max-width: 480px) {
    .pagination {
        width: 100%;
    }
    .arrow {
        margin: auto 0;
    }
}